<template>
  <div>
    <el-table
      :data="currentPageData"
      border
      highlight-current-row
      style="
        width: 100%;
        border-radius: 12px;
        margin-bottom: 1rem;
        font-size: 4px;
      "
    >
      <el-table-column type="index">
        <template slot-scope="scope">
          <span>
            {{ scope.$index + 1 + (currentPage - 1) * pageSize }}
          </span>
        </template>
      </el-table-column>
      <template v-for="(column, index) in columns">
        <el-table-column
          v-if="column.filterable"
          :key="'filterable-' + index"
          :prop="column.prop"
          :label="column.label"
          :filters="column.filters"
          :filter-method="filterHandler"
          :width="column.width"
          :sortable="column.sortable"
        >
          <template slot-scope="scope">
            <span @click="cellClick(scope.row, column)">
              {{ scope.row[column.prop] }}
            </span>
          </template>
        </el-table-column>

        <el-table-column
          v-else
          :key="'non-filterable-' + index"
          :prop="column.prop"
          :label="column.label"
          :width="column.width"
          :sortable="column.sortable"
        >
          <template slot-scope="scope">
            <span @click="cellClick(scope.row, column)">
              {{ scope.row[column.prop] }}
            </span>
          </template>
        </el-table-column>
      </template>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    // 
    columns: {
      type: Array,
      required: true,
    },
    // 
    currentPageData: {
      type: Array,
      required: true,
    },
    // 
    currentPage: {
      type: Number,
      required: true,
    },
    // 
    pageSize: {
      type: Number,
      required: true,
    },
  },
  methods: {
    // 
    cellClick(row, column) {
      this.$emit("cellClick", row, column);
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
  },
};
</script>
