<template>
  <div ref="chart"></div>
</template>

<script>
import * as d3 from "d3";
export default {
  name: "Demo",
  data() {
    return {
      // 
      parsedData: null,
      // 
      colorScale: null,
      outerRadius: null,
      innerRadius: null,
      width: null,
    };
  },
  // 
  mounted() {
    this.initColorScale();
    this.draw();
  },
  methods: {
    
    chart() {
      this.width = 964;
      this.outerRadius = this.width / 2;
      this.innerRadius = this.outerRadius - 170;

      const svg = d3
        .select(this.$refs.chart)
        .append("svg")
        .style("width", "100%")
        .style("height", "auto")
        .style("font", "10px sans-serif")
        .attr("viewBox", [
          -this.outerRadius,
          -this.outerRadius,
          this.width,
          this.width,
        ]);

      const cluster = d3
        .cluster()
        .size([360, this.innerRadius])
        .separation((a, b) => 1);

      const root = d3
        .hierarchy(this.parsedData, (d) => d.branchset)
        .sum((d) => (d.branchset ? 0 : 1))
        .sort(
          (a, b) =>
            a.value - b.value || d3.ascending(a.data.length, b.data.length)
        );

      cluster(root);

      this.setRadius(
        root,
        (root.data.length = 0),
        this.innerRadius / this.maxLength(root)
      );

      this.setColor(root);

      svg.append("g").call(this.legend);

      svg.append("style").text(`
  
  .link--active {
    stroke: #000 !important;
    stroke-width: 1.5px;
  }
  
  .link-extension--active {
    stroke-opacity: .6;
  }
  
  .label--active {
    font-weight: bold;
    cursor: pointer;
  }
  
  `);

      const linkExtension = svg
        .append("g")
        .attr("fill", "none")
        .attr("stroke", "#000")
        .attr("stroke-opacity", 0.25)
        .selectAll("path")
        .data(root.links().filter((d) => !d.target.children))
        .join("path")
        .each(function (d) {
          d.target.linkExtensionNode = this;
        })
        .attr("d", (d) => this.linkExtensionConstant(d));

      const link = svg
        .append("g")
        .attr("fill", "none")
        .attr("stroke", "#000")
        .selectAll("path")
        .data(root.links())
        .join("path")
        .each(function (d) {
          d.target.linkNode = this;
        })
        .attr("d", (d) => this.linkConstant(d))
        .attr("stroke", (d) => d.target.color);

      svg
        .append("g")
        .selectAll("text")
        .data(root.leaves())
        .join("text")
        .attr("dy", ".31em")
        .attr(
          "transform",
          (d) =>
            `rotate(${d.x - 90}) translate(${this.innerRadius + 4},0)${
              d.x < 180 ? "" : " rotate(180)"
            }`
        )
        .attr("text-anchor", (d) => (d.x < 180 ? "start" : "end"))
        .text((d) => d.data.name.replace(/_/g, " "))
        .on("mouseover", this.mouseovered(true))
        .on("mouseout", this.mouseovered(false))
        .on("click", (event, d) => this.handleLeafNodeClick(event, d));

    },

    // 
    update(checked) {
      const t = d3.transition().duration(750);
      this.linkExtension
        .transition(t)
        .attr(
          "d",
          checked
            ? this.linkExtensionVariable(d)
            : this.linkExtensionConstant(d)
        );
      link
        .transition(t)
        .attr("d", checked ? this.linkVariable(d) : this.linkConstant(d));
    },

    // 
    mouseovered(active) {
      return function (event, d) {
        d3.select(this).classed("label--active", active);
        d3.select(d.linkExtensionNode)
          .classed("link-extension--active", active)
          .raise();
        do d3.select(d.linkNode).classed("link--active", active).raise();
        while ((d = d.parent));
      };
    },

    // 
    handleLeafNodeClick(event, d) {
        console.log(d);
        window.open(`structure?cell=${d.data.name}`);
    },

    // 
    initColorScale() {
      this.colorScale = d3
        .scaleOrdinal()
        .domain(["Bacteria", "Eukaryota", "Archaea"])
        .range(d3.schemeCategory10);
    },

    // 
    legend(svg) {
      const g = svg
        .selectAll("g")
        .data(this.colorScale.domain())
        .join("g")
        .attr(
          "transform",
          (d, i) =>
            `translate(${-this.outerRadius},${-this.outerRadius + i * 20})`
        );

      g.append("rect")
        .attr("width", 18)
        .attr("height", 18)
        .attr("fill", this.colorScale);

      g.append("text")
        .attr("x", 24)
        .attr("y", 9)
        .attr("dy", "0.35em")
        .text((d) => d);
    },

    // 
    draw() {
      fetch(
        "https://gist.githubusercontent.com/mbostock/c034d66572fd6bd6815a/raw/98778537e42f5605d9eddae5fba3329d969b813c/life.txt"
      )
        .then((response) => {
          if (!response.ok) {
            throw new Error("Network response was not ok");
          }
          return response.text();
        })
        .then((data) => {
          this.parsedData = this.parseNewick(data);
          // Do something with the parsed data, e.g., render a chart
          this.chart();
        })
        .catch((error) => {
          console.error("There was a problem with the fetch operation:", error);
        });
    },

    // 
    linkStep(startAngle, startRadius, endAngle, endRadius) {
      const c0 = Math.cos((startAngle = ((startAngle - 90) / 180) * Math.PI));
      const s0 = Math.sin(startAngle);
      const c1 = Math.cos((endAngle = ((endAngle - 90) / 180) * Math.PI));
      const s1 = Math.sin(endAngle);
      return (
        "M" +
        startRadius * c0 +
        "," +
        startRadius * s0 +
        (endAngle === startAngle
          ? ""
          : "A" +
            startRadius +
            "," +
            startRadius +
            " 0 0 " +
            (endAngle > startAngle ? 1 : 0) +
            " " +
            startRadius * c1 +
            "," +
            startRadius * s1) +
        "L" +
        endRadius * c1 +
        "," +
        endRadius * s1
      );
    },

    // 
    linkExtensionConstant(d) {
      return this.linkStep(
        d.target.x,
        d.target.y,
        d.target.x,
        this.innerRadius
      );
    },

    // 
    linkExtensionVariable(d) {
      return this.linkStep(
        d.target.x,
        d.target.radius,
        d.target.x,
        this.innerRadius
      );
    },

    // 
    linkConstant(d) {
      return this.linkStep(d.source.x, d.source.y, d.target.x, d.target.y);
    },

    // 
    linkVariable(d) {
      return this.linkStep(
        d.source.x,
        d.source.radius,
        d.target.x,
        d.target.radius
      );
    },

    // 
    setColor(d) {
      var name = d.data.name;
      d.color =
        this.colorScale.domain().indexOf(name) >= 0
          ? this.colorScale(name)
          : d.parent
          ? d.parent.color
          : null;
      if (d.children) d.children.forEach((child) => this.setColor(child));
    },

    // 
    setRadius(d, y0, k) {
      d.radius = (y0 += d.data.length) * k;
      if (d.children) d.children.forEach((d) => this.setRadius(d, y0, k));
    },

    // 
    maxLength(d) {
      return (
        d.data.length + (d.children ? d3.max(d.children, this.maxLength) : 0)
      );
    },

    // Newick
    parseNewick(a) {
      for (
        var e = [], r = {}, s = a.split(/\s*(;|\(|\)|,|:)\s*/), t = 0;
        t < s.length;
        t++
      ) {
        var n = s[t];
        switch (n) {
          case "(":
            var c = {};
            (r.branchset = [c]), e.push(r), (r = c);
            break;
          case ",":
            var c = {};
            e[e.length - 1].branchset.push(c), (r = c);
            break;
          case ")":
            r = e.pop();
            break;
          case ":":
            break;
          default:
            var h = s[t - 1];
            ")" == h || "(" == h || "," == h
              ? (r.name = n)
              : ":" == h && (r.length = parseFloat(n));
        }
      }
      return r;
    },
  },
};
</script>

