<template>
  <div>
    <my-header></my-header>
    <div style="margin: 50px 0 50px 0">
      <div class="container">
        <div class="image-wrapper">
          <img
            src="https://www.google.com/images/branding/googlelogo/1x/googlelogo_color_272x92dp.png"
            alt="Image Description"
          />
        </div>
        <div class="text-wrapper" style="margin-left: 50px;">
          <h2>COVID-19: Latest Updates</h2>
          <p style="margin-top: 30px;">
            Stay informed about the ongoing global pandemic and the advancements
            in research, treatment, and vaccination.
          </p>
        </div>
      </div>
      <div>
        <el-main
          style="
            border-radius: 18px;
            border: 2px solid #eafbff;
            margin: 50px 20px 30px 20px;
          "
        >
          <el-row>
            <CustomTable
              :currentPageData="currentPageData"
              :pageSize="pageSize"
              :currentPage="currentPage"
              :columns="columns"
            ></CustomTable>
          </el-row>
          <el-row>
            <el-pagination
              @current-change="handleCurrentChange"
              @size-change="handleSizeChange"
              :page-size="pageSize"
              :pager-count="pagerCount"
              :current-page="currentPage"
              :total="total"
              :page-sizes="[10, 20, 30, 40]"
              layout="total, sizes, prev, pager, next"
              hide-on-single-page="false"
            >
            </el-pagination>
          </el-row>
        </el-main>
      </div>
    </div>
    <my-footer></my-footer>
  </div>
</template>

<script>
import CustomTable from "@/components/table/CustomTable.vue";
export default {
  components: {
    CustomTable,
  },
  data() {
    return {
      columns: [
        {
          prop: "drugname",
          label: "Drug name",
        },
        {
          prop: "drugtype",
          label: "Drug type",
        },
        {
          prop: "target",
          label: "Target",
        },
        {
          prop: "ic",
          label: "IC50 ",
        },
        {
          prop: "ec",
          label: "EC50",
        },
        {
          prop: "cc",
          label: "CC50",
        },
        {
          prop: "cellline",
          label: "Cell line",
        },
        {
          prop: "virusstrain",
          label: "Virus strain",
        },
        {
          prop: "pdb",
          label: "PDB",
        },
        {
          prop: "status",
          label: "Status",
        },
        {
          prop: "ref",
          label: "Ref.",
        },
      ],
      tableData: [],
      currentPageData: [],
      total: 0,
      pageSize: 10,
      pagerCount: 5,
      currentPage: 1,
    };
  },
  created() {
    this.getTableData();
  },
  methods: {
    async getTableData() {
      try {
        const res = await this.$http.get("/drugs/invitrodatasummary/tableData");
        this.tableData = res.data.data;
        this.total = this.tableData.length;
        this.updateTableData();
      } catch (error) {
        console.log(error);
      }
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val;
      this.updateTableData();
    },
    // 
    handleSizeChange(val) {
      this.pageSize = val;
      this.updateTableData();
    },
    // 
    updateTableData() {
      // 
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = startIndex + this.pageSize;
      this.currentPageData = this.tableData
        .slice(startIndex, endIndex)
        .map((item, index) => {
          item.index = startIndex + index + 1; // 
          return item;
        });
    },
  },
};
</script>

<style>
.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 100px;
  background: linear-gradient(135deg, #d1d7ed, #c6d7f0, #d4eff4);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.image-wrapper {
  flex: 1;
  margin-right: 20px;
  padding: 10px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
}

.image-wrapper img {
  width: 100%;
  border-radius: 8px;
}

.text-wrapper {
  flex: 2;
  color: #ffffff;
  animation: fadeInUp 1.5s ease;
}

.text-wrapper h2 {
  font-size: 24px;
  margin-bottom: 10px;
  font-weight: bold;
  background: -webkit-linear-gradient(
    45deg,
    #1e3c72,
    #2a5298,
    #2c3e50,
    #34495e
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: fadeInUp 1.5s ease;
}

.text-wrapper p {
  font-size: 18px;
  line-height: 1.5;
  background: -webkit-linear-gradient(
    45deg,
    #1e3c72,
    #2a5298,
    #2c3e50,
    #34495e
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: fadeInUp 1.5s ease;
}
</style>
