<!-- LineChart.vue -->
<template>
  <div>
    <canvas ref="chart"></canvas>
  </div>
</template>

<script>
import Chart from "chart.js/auto";

export default {
  name: "LineChart",
  props: {
    labels: Array,
    data: Array,
    backgroundColor: String,
    borderColor: String,
    chartTitle: String,
  },
  mounted() {
    this.createChart();
  },
  methods: {
    createChart() {
      const chartData = {
        labels: this.labels,
        datasets: [
          {
            label: "My Dataset",
            data: this.data,
            fill: false,
            backgroundColor: this.backgroundColor,
            borderColor: this.borderColor,
            borderWidth: 2,
            tension: 0.4,
          },
        ],
      };

      const chartOptions = {
        scales: {
          y: {
            beginAtZero: true,
          },
        },
        plugins: {
          title: {
            display: !!this.chartTitle,
            text: this.chartTitle,
          },
        },
      };

      new Chart(this.$refs.chart, {
        type: "line",
        data: chartData,
        options: chartOptions,
      });
    },
  },
};
</script>
