<template>
  <div class="container">
    <svg ref="svg"></svg>
  </div>
</template>
          
  <script>
import * as d3 from "d3";
import * as d3GeoProjection from "d3-geo-projection";
import * as bertin from "bertin";
export default {
  data() {
    return {
      countries: null,
      arraycountry: null,
      datacountry: null,
      geocountry: null,
    };
  },
  async mounted() {
    await this.getData();
    console.log("this.drawMap()");
    this.drawMap();
  },

  methods: {
    async getData() {
      console.log("this.countries");
      this.countries = require("@/assets/json/CNTR_RG_60M_2020_4326.json");
      console.log(this.countries);

      console.log("this.datacountry");
      this.datacountry = require("@/assets/json/datacountry.json");
      console.log(this.datacountry);

      console.log("this.arraycountry");
      console.log("this.datacountry.results");
      console.log(this.datacountry.results);
      console.log("this.datacountry.results.bindings");
      console.log(this.datacountry.results.bindings);

      console.log("this.arraycountryd");
      this.arraycountry = this.datacountry.results.bindings.map((d) => {
        console.log(d); //  d 

        return {
          property: d.property.value,
          propertyLabel: d.propertyLabel.value,
          count: parseInt(d.count.value),
          code_iso: d.code_iso.value,
        };
      });

      console.log("this.arraycountry");
      console.log(this.arraycountry);

      console.log("this.geocountry");

      this.geocountry = bertin.merge(
        this.countries,
        "ISO3_CODE",
        this.arraycountry,
        "code_iso"
      );

      console.log("1");

      console.log(this.geocountry);

      console.log("2");

      console.log("over");
    },

    drawMap() {
      const svg = d3
        .select(this.$refs.svg)
        .style("width", "100%")
        .style("height", "auto")
        .attr("viewBox", [0, 0, 1000, 650]);

      const params = {
        projection: d3GeoProjection.geoBertin1953(),
      };

      console.log("diddiididiidi");
      console.log(this.geocountry);
      console.log("nanannannanan");

      const layers = [
        {
          type: "bubble",
          geojson: this.geocountry,
          values: "count",
          k: 40,
          iteration: 100,
          fill: "#990000",
          tooltip: [
            (d) => d.properties.propertyLabel,
            (d) => d.properties.count,
          ],
          leg_x: 720,
          leg_y: 420,
          leg_title: "Viral mutations",
          leg_fill: "#990000",
        },
        {
          type: "layer",
          geojson: this.countries,
          fill: "white",
          fillOpacity: false ? 0.01 : 0.3,
          stroke: "none",
        },
        { type: "graticule" },
        { type: "outline", fillOpacity: false ? 0.01 : 1 },
      ];

      const mapSvg = bertin.draw({ params, layers });

      console.log("mapSvg");
      console.log(mapSvg);

      mapSvg.querySelectorAll("circle").forEach((bubble) => {
        bubble.addEventListener("click", () => {

          const bubbleData = bubble.__data__;

          const propertyLabel = bubbleData.properties.propertyLabel;
          const count = bubbleData.properties.count;

          console.log("propertyLabel:", propertyLabel);
          console.log("count:", count);

          window.open(`structure?cell=${propertyLabel}`); 
        });
      });

      //  SVG  DOM 
      this.$refs.svg.appendChild(mapSvg);
    },
  },
};
</script>
        
  <style scoped>
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  height: auto;
  margin-top: 40px;
}
</style>
          