<template>
    <div>
      <svg ref="svg" :width="width" :height="height"></svg>
    </div>
  </template>
    
    <script>
  import * as d3 from "d3";
  import { geoBertin1953 } from "d3-geo-projection";
  import { feature } from "topojson-client";
  
  export default {
    data() {
      return {
        width: 960,
        height: 500,
        worldData: null,
      };
    },
    async mounted() {
      this.fetchWorldData();
      this.drawMap();
    },
    methods: {
      fetchWorldData() {
        try {
          const world = require("@/assets/json/world-110m.json");
          this.worldData = feature(world, world.objects.countries).features;
        } catch (error) {
          console.error("Error fetching world data:", error);
        }
      },
      drawMap() {
        const svg = d3.select(this.$refs.svg);
        const projection = geoBertin1953().translate([
          this.width / 2,
          this.height / 2,
        ]);
        const path = d3.geoPath().projection(projection);
  
        svg
          .selectAll("path")
          .data(this.worldData)
          .enter()
          .append("path")
          .attr("d", path)
          .attr("fill", "#ccc")
          .attr("stroke", "#000");
      },
    },
  };
  </script>
    