import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/home/Home.vue";
import Sequences from "@/views/genomes/Sequences.vue";
import QuickStart from "@/views/manual/QuickStart.vue";
import Overview from "@/views/manual/Overview.vue";
import Download from "@/views/download/Download.vue";
import PhylogeneticTree from "@/views/genomes/PhylogeneticTree.vue";
import ViralMutationInfo from "@/views/genomes/ViralMutationInfo.vue";
import HostProteins from "@/views/proteins/HostProteins.vue";
import ViralProteins from "@/views/proteins/ViralProteins.vue";
import DrugCombinations from "@/views/drugs/DrugCombinations.vue";
import InVitroDataSummary from "@/views/drugs/InVitroDataSummary.vue";
import ClinicalTrialDrugs from "@/views/drugs/ClinicalTrialDrugs.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: Home,
  },
  {
    path: "/sequences",
    component: Sequences,
  },
  {
    path: "/home",
    component: Home,
  },
  {
    path: "/download",
    component: Download,
  },
  {
    path: "/overview",
    component: Overview,
  },
  {
    path: "/quickstart",
    component: QuickStart,
  },
  {
    path: "/phylogenetictree",
    component: PhylogeneticTree,
  },
  {
    path: "/viralmutationinfo",
    component: ViralMutationInfo,
  },
  {
    path: "/viralproteins",
    component: ViralProteins,
  },
  {
    path: "/hostproteins",
    component: HostProteins,
  },
  {
    path: "/drugcombinations",
    component: DrugCombinations,
  },
  {
    path: "/invitrodatasummary",
    component: InVitroDataSummary,
  },
  {
    path: "/clinicaltrialdrugs",
    component: ClinicalTrialDrugs,
  },
  {
    path: "/structure",
    component: () => import("@/components/visualization/Structure.vue"),
  },
  {
    path: "/*",
    redirect: {
      path: "/",
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
