<template>
  <div>
    <my-header></my-header>
    <div>
      <el-main style="border-radius: 18px; border: 2px solid #eafbff;margin: 40px;">
        <el-row>
          <CustomTable
            :currentPageData="currentPageData"
            :pageSize="pageSize"
            :currentPage="currentPage"
            :columns="columns"
          ></CustomTable>
        </el-row>
        <el-row>
          <el-pagination
            @current-change="handleCurrentChange"
            @size-change="handleSizeChange"
            :page-size="pageSize"
            :pager-count="pagerCount"
            :current-page="currentPage"
            :total="total"
            :page-sizes="[10, 20, 30, 40]"
            layout="total, sizes, prev, pager, next"
            hide-on-single-page="false"
          >
          </el-pagination>
        </el-row>
      </el-main>
    </div>
    <my-footer></my-footer>
  </div>
</template>

<script>
import CustomTable from "@/components/table/CustomTable.vue";
export default {
  components: {
    CustomTable,
  },
  data() {
    return {
      columns: [
        { label: "Organism", prop: "organism" },
        { label: "Lineage", prop: "lineage" },
        { label: "mutations", prop: "mutations" },
        { label: "Gene", prop: "gene" },
        { label: "Amino acid(AA) substitution", prop: "substitution" },
        { label: "Protein", prop: "protein" },
        { label: "Description", prop: "description" },
        { label: "Ref", prop: "ref" },
      ],
      tableData: [],
      currentPageData: [],
      total: 0,
      pageSize: 10,
      pagerCount: 5,
      currentPage: 1,
    };
  },
  created() {
    this.getTableData();
  },
  methods: {
    async getTableData() {
      try {
        const res = await this.$http.get(
          "/genomes/viralmutationinfo/tableData"
        );
        this.tableData = res.data.data;
        this.total = this.tableData.length;
        this.updateTableData();
      } catch (error) {
        console.log(error);
      }
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val;
      this.updateTableData();
    },
    // 
    handleSizeChange(val) {
      this.pageSize = val;
      this.updateTableData();
    },
    // 
    updateTableData() {
      // 
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = startIndex + this.pageSize;
      this.currentPageData = this.tableData
        .slice(startIndex, endIndex)
        .map((item, index) => {
          item.index = startIndex + index + 1; // 
          return item;
        });
    },
  },
};
</script>

